
.header{
    display: flex;
    justify-content: space-between;
   
    height: 160px;
background-color:#111111;
}
.gidOnline{
font-size: 170px;
color: rgba(100, 100, 100, 0.2);
margin: 0 auto;

width: 100%;
text-align:center;


}

.header__wrap_left_block{
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
margin: 40px 0 40px 110px;
}

.navigation{
  
    
    display: flex;
    justify-content:flex-start;

}
.navigation_item{

    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;
     margin-left: 10px;
     border-right: 1px solid #5c5a5a;
     padding-right: 12px;
     color: #5c5a5a;
    }
   
.navigation_item:first-child{
    margin-left: 0;
  
}
.navigation_item:last-child{
    border-right:none;
  
}
.navLink{
    text-decoration: none;}

